/*#################################################
Profile
#################################################*/
.profile {
  display: flex;
  box-sizing: border-box;
  background: rgb(238, 238, 238);
  padding: 33px 33px 22px 33px;
}

/*#################################################
Column-1
#################################################*/
.col-profile1 {
  box-sizing: border-box;
  display: inline-block;
  padding: 0 15px;
  background: rgb(238, 238, 238);
}

.photo {
  box-sizing: border-box;
  color: rgb(0, 0, 0);
  margin-top: 25px;
  margin-bottom: 20px;
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
}

img {
  width: 100%;
  margin: auto;
}

a {
  background-attachment: scroll;
  box-sizing: border-box;
  color: rgb(0, 0, 0);
  cursor: pointer;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 16px;
  letter-spacing: 0.5px;
  line-height: 22.8571px;
  text-decoration-color: rgb(0, 0, 0);
  text-decoration-line: none;
  text-decoration-style: solid;
  display: block;
}
.download-resume {
  display: block;
  padding: 17px 5px;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  transition: all 0.3s;
}

.download {
  display: block;
  text-decoration: none;

  font-size: 16px;
}

.icon-cloud {
  display: block;
  content: "\f0ed";
}

.availabel {
  display: block;
  text-align: center;
  font-size: 14px;
  margin-top: 15px;
}
/*#################################################
Column-2 
#################################################*/
.col-profile2 {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0 15px;
  background: rgb(238, 238, 238);
  display: flex;
}
.info-profile {
  box-sizing: border-box;
  background: rgb(238, 238, 238);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.h1-header {
  color: rgb(117, 117, 117);
  margin-bottom: -15px;
}
.h1-header span {
  color: rgb(0, 0, 0);
}

p {
  max-width: 500px;
}

li {
  list-style: none;
}

.personal-info {
  box-sizing: border-box;
  background: rgb(255, 255, 255);
}

.personal-info ul li span {
  font-size: 16px;
  margin-right: 5px;
  color: rgb(151, 151, 151);
}

@media (max-width: 767px) {
  .info-profile {
    align-items: center;
    margin-top: 20px;
  }
  
  p {
    max-width: 500px;
    text-align: center;
  } 
}

/*----------Social Links----------
#################################*/

.title-links {
  display: block;
  margin-left: 10px;
}

.social-icons-3 {
  box-sizing: border-box;
  padding: 0;
  list-style: none;
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}

.social-icons-3 li {
  margin: 10px 5px;
  display: inline-block;
}
.social-icons-3 a i {
  font-size: 24px;
  line-height: 48px;
  transition: 0.3s;
}

.social-icons-3 a {
  text-decoration: none;
  position: relative;
  display: inline-block;
  width: 48px;
  height: 48px;
  background: #e6e6e6;
  text-align: center;
  margin: 0 8px;
  box-shadow: 5px 0 5px rgba(0, 0, 0, 0.3);
  transition: 0.3s;
  color: #575757;
}

.social-icons-3 a:hover {
  transform: translate(-0.5%, 0.5%);
  box-shadow: 3px 2px 10px rgba(0, 0, 0, 0.5);
}
.social-icons-3 a:hover {
  background: #ffffff;
}
.social-icons-3 a:hover i.fa-youtube {
  color: rgb(255, 0, 0);
}
.social-icons-3 a:hover i.fa-twitter {
  color: rgb(29, 161, 242);
}
.social-icons-3 a:hover i.fa-telegram {
  color: #2e87ca;
}
.social-icons-3 a:hover i.fa-facebook-f {
  color: #3b5998;
}
.social-icons-3 a:hover i.fa-linkedin-in {
  color: rgb(0, 119, 181);
}
.social-icons-3 a:hover i.fa-github {
  color: rgb(0, 0, 0);
}
.social-icons-3 a:hover::after {
  background: #5c5c5c;
}
.social-icons-3 a:hover::before {
  background: #979797;
}

.li-social-link {
  display: inline-block;
  margin: 5px;
}
